
.bg-primary {
  background: #3da8f5;
}
.color-danger {
  color: #E53935;
}
.color-gary {
  color: #636363;
}
.logo {
  -ms-flex-item-align: center !important;
      align-self: center !important;
  position: absolute;
  left: 40%;
  right: 40%;
  width: 20%;
  text-align: center;
}
.logo img {
    height: 30px;
}
.drop-down-projects, .drop-down-user {
  min-height: 300px;
  width: 200px;
}
.drop-down-projects .title, .drop-down-user .title {
    font-size: 14px;
    text-align: left;
    padding-left: 20px;
    line-height: 40px;
    border-bottom: 1px solid #EEEEEE;
    color: #424242;
    margin: 0;
}
.drop-down-user {
  width: 250px;
}
.drop-down-user .footer-end {
    margin-top: 200px;
}
.nav-toggle-teams {
  color: #636363;
  margin: 0 10px;
  font-size: 30px;
  line-height: 30px;
}
.notice-box {
  margin-top: 25px;
  margin-left: 25px;
}
.menu-navbar {
  position: relative;
  z-index: 100;
  height: 58px;
  background-color: rgba(255, 255, 255, 0.95);
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
  padding: 4px 10px;
}
.menu-navbar .user-head-img {
    border-radius: 50%;
    display: inline-block;
    height: 40px;
    margin-top: 5px;
    margin-left: 10px;
    width: 40px;
}
.menu-navbar .nav-item .split-border {
    vertical-align: middle;
    line-height: 30px;
    height: 30px;
    display: block;
    margin-top: 10px;
}
.menu-navbar .nav-item .nav-link {
    line-height: 50px;
    margin: 0;
    padding: 0 10px;
    font-size: 15px;
}
.menu-navbar .nav-item .nav-link .ion {
      font-size: 24px;
      line-height: 50px;
}
.ion-md {
  font-weight: normal;
  font-size: 28px;
  width: 30px;
  text-align: center;
  vertical-align: middle;
  line-height: 30px;
  height: 30px;
  padding: 0;
  cursor: pointer;
}
.ion-lg {
  font-size: 25px;
}
.full-loading {
  position: fixed;
  text-align: center;
  height: 100%;
  width: 100%;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 1001;
  padding-top: 10%;
}
.full-loading .load-view {
    position: relative;
    margin: 0 auto;
}
.el-icon-loading {
  font-size: 50px;
  margin: 10px;
  color: #3da8f5;
}



